import anime from 'animejs/lib/anime.es.js';


const greetingText = `Hello, I'm <span class="greeting-text-container-text-span">Pavel Popov</span>.`
const greetingTextSecondPart = `I'm a junior (student) Front-end developer.`
const body = document.querySelector(`body`)

export function generateGreetingScreen() {
      // console.log(`greeting screen generate`)

      const greetingScreenContainer = document.createElement('div')
      greetingScreenContainer.classList.add(`greeting-screen-container`)
      body.insertAdjacentElement('beforeend', greetingScreenContainer)

      const greetingScreenContainerBackground = document.createElement('div')
      greetingScreenContainerBackground.classList.add('greeting-screen-container-background')
      greetingScreenContainer.insertAdjacentElement('beforeend', greetingScreenContainerBackground)

      //    PHOTO
      const photoContainer = document.createElement('div')
      photoContainer.classList.add(`phoro-container`)
      greetingScreenContainer.insertAdjacentElement('beforeend', photoContainer)

      //MID CONTAINER
      const midContainer = document.createElement('div')
      midContainer.classList.add('mid-container')
      greetingScreenContainer.insertAdjacentElement('beforeend', midContainer)

      //    CV BUTTON
      const cvButtonContainer = document.createElement('div')
      cvButtonContainer.classList.add('cv-button-container')
      midContainer.insertAdjacentElement('beforeend', cvButtonContainer)

      const cvButton = document.createElement('div')
      cvButton.classList.add('cv-button')
      cvButtonContainer.insertAdjacentElement('beforeend', cvButton)

      const cvButtonText = document.createElement('p')
      cvButtonText.classList.add('cv-button-text')
      cvButtonText.textContent = `CV`
      cvButton.insertAdjacentElement('beforeend', cvButtonText)

      //    GREETING TEXT CONTAINER
      const greetingTextContainer = document.createElement(`div`)
      greetingTextContainer.classList.add('greeting-text-container')
      midContainer.insertAdjacentElement('beforeend', greetingTextContainer)

      const greetingTextContainerText = document.createElement(`p`)
      greetingTextContainerText.classList.add('greeting-text-container-text')
      greetingTextContainerText.innerHTML = greetingText
      greetingTextContainer.insertAdjacentElement('beforeend', greetingTextContainerText)

      const greetingTextContainerTextSecondPart = document.createElement(`p`)
      greetingTextContainerTextSecondPart.classList.add('greeting-text-container-text-second')
      greetingTextContainerTextSecondPart.innerHTML = greetingTextSecondPart
      greetingTextContainer.insertAdjacentElement('beforeend', greetingTextContainerTextSecondPart)

      //    PROJECTS BUTTON
      const projectsButtonContainer = document.createElement('div')
      projectsButtonContainer.classList.add('projects-button-container')
      midContainer.insertAdjacentElement('beforeend', projectsButtonContainer)

      const projectsButton = document.createElement('div')
      projectsButton.classList.add('projects-button')
      projectsButtonContainer.insertAdjacentElement('beforeend', projectsButton)

      const projectsButtonText = document.createElement('p')
      projectsButtonText.classList.add('projects-button-text')
      projectsButtonText.textContent = `PROJECTS`
      projectsButton.insertAdjacentElement('beforeend', projectsButtonText)

      //    INFO BUTTON
      const moreinfoButtonContainer = document.createElement('div')
      moreinfoButtonContainer.classList.add('moreinfo-button-container')
      greetingScreenContainer.insertAdjacentElement('beforeend', moreinfoButtonContainer)

      const moreinfoButton = document.createElement('div')
      moreinfoButton.classList.add('moreinfo-button')
      moreinfoButtonContainer.insertAdjacentElement('beforeend', moreinfoButton)

      const moreinfoButtonText = document.createElement('p')
      moreinfoButtonText.classList.add('moreinfo-button-text')
      moreinfoButtonText.textContent = `ABOUT ME`
      moreinfoButton.insertAdjacentElement('beforeend', moreinfoButtonText)

      const moreinfoButtonArrow = document.createElement('img')
      moreinfoButtonArrow.classList.add('moreinfo-button-arrow')
      moreinfoButtonArrow.src = `assets/img/arrowRevBlack.png`
      moreinfoButton.insertAdjacentElement('beforeend', moreinfoButtonArrow)

      anime({
            targets: '.moreinfo-button-arrow',
            keyframes: [
                  {translateY: 0},
                  {translateY: 15},
                  {translateY: -15},
                  {translateY: 0},
                  ],
            duration: 1300,
            easing: 'spring(1, 80, 10, 0)',
            loop: true,
            autoplay: true,
      });
}