export function generateContactBlock() {
      const contactWrapper = document.querySelector(`.moreinfo-screen-contact`)

      const contactTextBlock = document.createElement('div')
      contactTextBlock.classList.add('contact-textBlock')
      contactWrapper.insertAdjacentElement('beforeend', contactTextBlock)

      const contactTextBlockTitle = document.createElement('p')
      contactTextBlockTitle.classList.add('contact-textBlock-title')
      contactTextBlockTitle.textContent = `DROP ME A LINE`
      contactTextBlock.insertAdjacentElement('beforeend', contactTextBlockTitle)

      const contactTextBlockText = document.createElement('p')
      contactTextBlockText.classList.add('contact-textBlock-text')
      contactTextBlockText.textContent = `No one lights a lamp in order to hide it behind the door: the purpose of light is to create more light, to open people’s eyes, to reveal the marvels around.`
      contactTextBlock.insertAdjacentElement('beforeend', contactTextBlockText)



      const contactForm = document.createElement('form')
      contactForm.classList.add('contact-form')
      contactForm.id = 'my-form'
      contactForm.method = 'POST'
      contactForm.action = 'https://formspree.io/f/xrgowpyn'
      contactTextBlock.insertAdjacentElement('beforeend', contactForm)

      const contactFormName = document.createElement('input')
      contactFormName.classList.add('contact-form-name')
      contactFormName.type = `text`
      contactFormName.placeholder = `Name`
      contactForm.insertAdjacentElement('beforeend', contactFormName)

      const contactFormMail = document.createElement('input')
      contactFormMail.classList.add('contact-form-mail')
      contactFormMail.type = `email`
      contactFormMail.placeholder = `E-mail`
      contactFormMail.name = '_replyto'
      contactFormMail.name = 'email'
      contactForm.insertAdjacentElement('beforeend', contactFormMail)

      const contactFormMailBotBlock = document.createElement('input')
      contactFormMailBotBlock.type = 'text'
      contactFormMailBotBlock.name = '_gotcha'
      contactFormMailBotBlock.style = 'display:none'

      const contactFormTextarea = document.createElement('textarea')
      contactFormTextarea.name = 'message'
      contactFormTextarea.placeholder = `Message`
      contactFormTextarea.classList.add('contact-form-text')
      contactForm.insertAdjacentElement('beforeend', contactFormTextarea)

      const contactFormButton = document.createElement('input')
      contactFormButton.classList.add('contact-form-button')
      contactFormButton.type = 'button'
      contactFormButton.value = `SEND MESSAGE`
      contactForm.insertAdjacentElement('beforeend', contactFormButton)

      const contactFormButtonText = document.createElement('p')
      contactFormButtonText.classList.add('contact-form-button-text')
      contactFormButtonText.textContent = `SEND MESSAGE`
      contactFormButton.insertAdjacentElement('beforeend', contactFormButtonText)
}