import { generateMoreinfoScreen } from "../moreinfo-screen/generateMoreinfoScreen";
// import { generateSkillsBlock } from "../moreinfo-screen/skills";
import { generateCvScreen } from "../cv-screen/generateCvScreen";
import { generateProjectsScreen } from "../projects/generateProjectsScreen";

      //    SCROLL
function skrollToSkills() {
      let complite = 'complite'
      document.querySelector('.moreinfo-screen-skills').scrollIntoView({
            behavior: 'smooth',
            block: 'start'
      })
      return complite
}

      //    BUTTONS HOVER
document.body.addEventListener('mouseover', (e)=> {
      if (e.srcElement.classList[0] === 'cv-button') {
            const cvButton = document.querySelector('.cv-button')
            cvButton.classList.add('cv-button-hover')

            let cvPreview = document.createElement('img')
            cvPreview.src = 'assets/img/cv_prev.png'
            cvPreview.alt = 'cv preview image'
            cvPreview.classList.add('cv-preview')
            cvPreview.classList.add('cv-preview-hide')
            cvButton.insertAdjacentElement('beforeend', cvPreview)

            setTimeout(() => {
                  cvPreview.classList.add('cv-preview-open')
            }, 300);
      }

      if (e.srcElement.classList[0] === 'projects-button') {
            const projectsButton = document.querySelector('.projects-button')
            projectsButton.classList.add('projects-button-hover')

            let projectsPreview = document.createElement('img')
            projectsPreview.src = 'assets/img/projects_prev.png'
            projectsPreview.alt = 'projects preview image'
            projectsPreview.classList.add('projects-preview')
            projectsPreview.classList.add('projects-preview-hide')
            projectsButton.insertAdjacentElement('beforeend', projectsPreview)

            setTimeout(() => {
                  projectsPreview.classList.add('projects-preview-open')
            }, 300);
      }

      if (e.srcElement.classList[0] === 'moreinfo-button') {
            const moreinfoButton = document.querySelector('.moreinfo-button')
            moreinfoButton.classList.add('moreinfo-button-hover')
            

            setTimeout(() => {
                  const moreinfoButtonArrow = document.querySelector(`.moreinfo-button-arrow`) 
                  moreinfoButtonArrow.classList.add('show-arrow')
            }, 200);
      }
})



      //    BUTTONS HOVER REMOVE 
document.body.addEventListener('mouseout', (e)=> {
      const cvPreview = document.querySelector('.cv-preview')
      const cvButton = document.querySelector('.cv-button')

      const projectsButton = document.querySelector('.projects-button')
      const projectsPreview = document.querySelector(`.projects-preview`)

      const moreinfoButton = document.querySelector('.moreinfo-button')
      const moreinfoButtonArrow = document.querySelector(`.moreinfo-button-arrow`) 

      if (e.srcElement.classList[0] === 'cv-button') {
            cvPreview.classList.add('cv-preview-hide')
            cvPreview.classList.remove('cv-preview-open')
            setTimeout(() => {
                  cvButton.classList.remove('cv-button-hover')
                  cvButton.innerHTML = '<p class="cv-button-text">CV</p>'
            }, 200);
      }

      if (e.srcElement.classList[0] === 'projects-button') {
            projectsPreview.classList.add(`projects-preview-hide`)
            projectsPreview.classList.remove('projects-preview-open')
            setTimeout(() => {
                  projectsButton.classList.remove('projects-button-hover')
                  projectsButton.innerHTML = '<p class="projects-button-text">PROJECTS</p>'
            }, 200);
      }

      if (e.srcElement.classList[0] === 'moreinfo-button') {
            moreinfoButtonArrow.classList.remove('show-arrow')
            setTimeout(() => {
                  moreinfoButton.classList.remove('moreinfo-button-hover')
            }, 200);
      }
})



      //    BUTTONS CLICK
document.body.addEventListener('click', (e)=> {
      // console.log(e)
      const cvPreview = document.querySelector('.cv-preview')
      const cvButton = document.querySelector('.cv-button')

      const projectsButton = document.querySelector('.projects-button')
      const projectsPreview = document.querySelector(`.projects-preview`)

      const moreinfoButtonArrow = document.querySelector(`.moreinfo-button-arrow`)
      
      if (e.srcElement.classList[0] === 'cv-button') {
            cvButton.classList.add('cv-button-click')
            cvPreview.classList.add('cv-preview-hide')
            moreinfoButtonArrow.classList.remove('show-arrow')
            setTimeout(() => {
                  generateCvScreen()
            }, 500);
      }

      if (e.srcElement.classList[0] === 'projects-button') {
            projectsButton.classList.add(`projects-button-click`)
            projectsPreview.classList.remove(`projects-preview-hide`)
            moreinfoButtonArrow.classList.remove('show-arrow')
            setTimeout(() => {
                  generateProjectsScreen()
            }, 500);
      }

      if (e.srcElement.classList[0] === 'moreinfo-button') {
            if (document.body.querySelector('.moreinfo-screen-container') === null) {
                  generateMoreinfoScreen()
            }
            setTimeout(() => {
                  skrollToSkills()       
            }, 50);
      }
})


      //    REMOVE HOVER CHECK
document.body.addEventListener('mousemove', (e)=> {
      const moreinfoButtonArrow = document.querySelector(`.moreinfo-button-arrow`)
      if (e.srcElement.classList[0] != 'moreinfo-button') {
            moreinfoButtonArrow.classList.remove('show-arrow')
      }

})