
//    click
document.body.addEventListener(`click`, (e)=> {
      console.log(e)

      if (e.srcElement.classList[0] === 'projects-screen-container-footer-container-backbuttonwrapper-button') {
            const projectScreen = document.querySelector(`.projects-screen-container`)
            projectScreen.classList.remove('show-projects-screen')

            const projectsButton = document.querySelector('.projects-button')
            setTimeout(() => {
                  projectsButton.classList.remove('projects-button-click')
                  setTimeout(() => {
                        projectScreen.remove()
                  }, 200);
            }, 1000);
      }
})


document.body.addEventListener(`mouseover`, (e)=> {
      if (e.srcElement.classList[0] === 'projects-screen-container-footer-container-backbuttonwrapper-button') {
            const projectsBackButton = document.querySelector(`.projects-screen-container-footer-container-backbuttonwrapper-button`)
            const projectsBackButtonText = document.querySelector(`.projects-screen-container-footer-container-backbuttonwrapper-button-text`)

            projectsBackButton.classList.add(`projects-screen-container-footer-container-backbuttonwrapper-button-hover`)
            projectsBackButtonText.classList.add(`projects-screen-container-footer-container-backbuttonwrapper-button-text-hover`)
      }

      if (e.srcElement.classList[0] === `project-container-wrapper-buttons-demo`) {
            e.target.classList.add(`project-container-wrapper-buttons-demo-hover`)
      }
      if (e.srcElement.classList[0] === `project-container-wrapper-buttons-git`) {
            e.target.classList.add(`project-container-wrapper-buttons-demo-hover`)
      }

})

document.body.addEventListener(`mouseout`, (e)=> {
      if (e.srcElement.classList[0] === 'projects-screen-container-footer-container-backbuttonwrapper-button') {
            const projectsBackButton = document.querySelector(`.projects-screen-container-footer-container-backbuttonwrapper-button`)
            const projectsBackButtonText = document.querySelector(`.projects-screen-container-footer-container-backbuttonwrapper-button-text`)

            projectsBackButton.classList.remove(`projects-screen-container-footer-container-backbuttonwrapper-button-hover`)
            projectsBackButtonText.classList.remove(`projects-screen-container-footer-container-backbuttonwrapper-button-text-hover`)
      }

      if (e.srcElement.classList[0] === `project-container-wrapper-buttons-demo`) {
            e.target.classList.remove(`project-container-wrapper-buttons-demo-hover`)
      }
      if (e.srcElement.classList[0] === `project-container-wrapper-buttons-git`) {
            e.target.classList.remove(`project-container-wrapper-buttons-demo-hover`)
      }
})