
function removeMoreinfoScreen() {
      document.querySelector(`.moreinfo-screen-container`).remove()
}

document.body.addEventListener('click', (e)=> {
      if (e.srcElement.innerHTML === 'HOME') {
            document.querySelector('.greeting-screen-container').scrollIntoView({
                  behavior: 'smooth',
                  block: 'start'
            })
      }
      if (e.srcElement.innerHTML === 'SKILLS') {
            document.querySelector('.moreinfo-screen-skills').scrollIntoView({
                  behavior: 'smooth',
                  block: 'start'
            })
      }
      if (e.srcElement.innerHTML === 'CONTACT') {
            document.querySelector('.moreinfo-screen-contact').scrollIntoView({
                  behavior: 'smooth',
                  block: 'start'
            })
      }
})

window.addEventListener('scroll', function() {
      if (pageYOffset === 0){
            removeMoreinfoScreen()
      }
});