

document.body.addEventListener('mouseover', (e)=> {
      // console.log(e)

      if (e.srcElement.classList[0] === 'contact-form-button') {
            const contactButton = document.querySelector('.contact-form-button')
            contactButton.classList.add('contact-form-button-hover')
      }
})

document.body.addEventListener('mouseout', (e)=> {
      // console.log(e)

      if (e.srcElement.classList[0] === 'contact-form-button') {
            const contactButton = document.querySelector('.contact-form-button')
            contactButton.classList.remove('contact-form-button-hover')
      }
})


document.body.addEventListener('click', (e)=> {
      // console.log(e)

      if (e.srcElement.classList[0] === 'contact-form-button') {
            // const contactButton = document.querySelector('.contact-form-button')
            const contactForm = document.querySelector('.contact-form')
            const contactFormName = document.querySelector('.contact-form-name')
            const contactFormMail = document.querySelector('.contact-form-mail')
            const contactMessage = document.querySelector('.contact-form-text')

            if (contactFormName.value.length !== 0 && contactFormMail.value.length !== 0 && contactMessage.value.length !== 0) {
                  contactForm.submit();
                  contactFormName.classList.remove('contact-form-name-red')
                  contactFormName.classList.remove('contact-form-name-red::placeholder')
                  contactFormName.placeholder = `Name`

                  contactFormMail.classList.remove('contact-form-mail-red')
                  contactFormMail.classList.remove('contact-form-mail-red::placeholder')
                  contactFormMail.placeholder = `Mail`

                  contactMessage.placeholder = `Message`
                  contactMessage.classList.remove('contact-form-text-red')
                  contactMessage.classList.remove('contact-form-text-red::placeholder')
            }else {
                  contactFormName.placeholder = `Enter your name`
                  contactFormName.classList.add('contact-form-name-red')
                  contactFormName.classList.add('contact-form-name-red::placeholder')

                  contactFormMail.placeholder = `Enter your e-mail`
                  contactFormMail.classList.add('contact-form-mail-red')
                  contactFormMail.classList.add('contact-form-mail-red::placeholder')

                  contactMessage.placeholder = `Can't be empty`
                  contactMessage.classList.add('contact-form-text-red')
                  contactMessage.classList.add('contact-form-text-red::placeholder')
            }
      }
})