

document.body.addEventListener('click', (e)=> {
      // console.log(e)
      if (e.srcElement.classList[0] === 'cv-footer-container-backbuttonwrapper-button') {
            // console.log(`hello cv back`)    

            const cvScreen = document.querySelector('.cv-screen-container')
            cvScreen.classList.remove('show-cv-screen')
      
            const cvButton = document.querySelector('.cv-button')
            setTimeout(() => {
                  cvButton.classList.remove('cv-button-click')
                  setTimeout(() => {
                        cvScreen.remove()
                  }, 200);
                  
            }, 1000);
            
            const cvFooter = document.querySelector('.cv-footer')
            cvFooter.classList.add('cv-footer-click')
      }
})

document.body.addEventListener('mouseover', (e)=> {
      if (e.srcElement.classList[0] === 'cv-footer-container-backbuttonwrapper-button') {
            const backButton = document.querySelector('.cv-footer-container-backbuttonwrapper-button')
            const backButtonText = document.querySelector(`.cv-footer-container-backbuttonwrapper-button-text`)
            const cvScreen = document.querySelector('.cv-screen-container')

            backButton.classList.add('cv-footer-container-backbuttonwrapper-button-hover')
            cvScreen.classList.add('cv-screen-container-hover')
            backButtonText.classList.add('cv-footer-container-backbuttonwrapper-button-text-hover')
      }

      if (e.srcElement.classList[0] === 'cv-footer-container-dawnloadbuttonwrapper-button') {
            const downloadButton = document.querySelector('.cv-footer-container-dawnloadbuttonwrapper-button')
            downloadButton.classList.add('cv-footer-container-dawnloadbuttonwrapper-button-hover')
      }
})

document.body.addEventListener('mouseout', (e)=> {
      if (e.srcElement.classList[0] === 'cv-footer-container-backbuttonwrapper-button') {
            const backButton = document.querySelector('.cv-footer-container-backbuttonwrapper-button')
            const backButtonText = document.querySelector(`.cv-footer-container-backbuttonwrapper-button-text`)
            const cvScreen = document.querySelector('.cv-screen-container')

            backButton.classList.remove('cv-footer-container-backbuttonwrapper-button-hover')
            cvScreen.classList.remove('cv-screen-container-hover')
            backButtonText.classList.remove('cv-footer-container-backbuttonwrapper-button-text-hover')
      }

      if (e.srcElement.classList[0] === 'cv-footer-container-dawnloadbuttonwrapper-button') {
            const downloadButton = document.querySelector('.cv-footer-container-dawnloadbuttonwrapper-button')
            downloadButton.classList.remove('cv-footer-container-dawnloadbuttonwrapper-button-hover')
      }
})