      //          START HOVER
document.body.addEventListener('mouseover', (e)=> {

      const webProgImg = document.querySelector('.skills-webProgramm-img')
      const webProg = document.querySelector('.skills-webProgramm')
      const webProgTextBlock = document.querySelector(`.skills-webProgramm-textWrapper`)

      const photoImg = document.querySelector('.skills-photoVideo-img')
      const photo = document.querySelector('.skills-photoVideo')
      const photoTextBlock = document.querySelector(`.skills-photoVideo-textWrapper`)

      const audioImg = document.querySelector('.skills-audio-img')
      const audio = document.querySelector('.skills-audio')
      const audioTextBlock = document.querySelector(`.skills-audio-textWrapper`)

      const musicImg = document.querySelector('.skills-music-img')
      const musicTextBlock = document.querySelector(`.skills-music-textWrapper`)


      
      if (e.srcElement.classList[0] === 'skills-webProgramm-img') {
            setTimeout(() => {
                  webProgImg.classList.add('skills-img-click')
                  webProg.classList.remove('small-size')
                  
                  photoImg.classList.remove('skills-img-click')
                  photo.classList.remove('skills-click')
                  photo.classList.remove(`small-size-middle`)
                  photoTextBlock.classList.remove('text-visible')

                  audioImg.classList.remove('skills-img-click')
                  audio.classList.remove('skills-click')
                  audio.classList.remove(`small-size`)
                  audioTextBlock.classList.remove('text-visible')

                  musicImg.classList.remove('skills-img-click')
                  musicTextBlock.classList.remove('text-visible')

            }, 50);
      }
      if (e.srcElement.classList[0] === 'skills-photoVideo-img') {
            setTimeout(() => {
                  photoImg.classList.add('skills-img-click')
                  photo.classList.remove('small-size-middle')
                  
                  webProgImg.classList.remove('skills-img-click')
                  webProg.classList.remove('skills-click')
                  webProgTextBlock.classList.remove('text-visible')

                  audioImg.classList.remove('skills-img-click')
                  audio.classList.remove('skills-click')
                  audio.classList.remove(`small-size`)
                  audioTextBlock.classList.remove('text-visible')

                  musicImg.classList.remove('skills-img-click')
                  musicTextBlock.classList.remove('text-visible')

            }, 50);
      }
      if (e.srcElement.classList[0] === 'skills-audio-img') {
            setTimeout(() => {
                  audioImg.classList.add('skills-img-click')
                  audio.classList.remove(`small-size`)

                  photoImg.classList.remove('skills-img-click')
                  photo.classList.remove('skills-click')
                  photoTextBlock.classList.remove('text-visible')
                  photo.classList.remove('small-size-middle')

                  musicImg.classList.remove('skills-img-click')
                  musicTextBlock.classList.remove(`text-visible`)

                  webProgImg.classList.remove('skills-img-click')
                  webProg.classList.remove('small-size')
                  webProg.classList.remove('skills-click')
                  webProgTextBlock.classList.remove('text-visible')

            }, 50);
      }
      if (e.srcElement.classList[0] === 'skills-music-img') {
            setTimeout(() => {
                  musicImg.classList.add('skills-img-click')

                  audioImg.classList.remove('skills-img-click')
                  audio.classList.remove('skills-click')
                  audio.classList.remove(`small-size`)
                  audioTextBlock.classList.remove('text-visible')

                  photoImg.classList.remove('skills-img-click')
                  photo.classList.remove('skills-click')
                  photoTextBlock.classList.remove('text-visible')
                  photo.classList.remove('small-size-middle')

                  webProgImg.classList.remove('skills-img-click')
                  webProg.classList.remove('small-size')
                  webProg.classList.remove('skills-click')
                  webProgTextBlock.classList.remove('text-visible')

            }, 100);
      }
})

      //          CLICK

document.body.addEventListener('click', (e)=> {
      const webProgImg = document.querySelector('.skills-webProgramm-img')
      const webProg = document.querySelector('.skills-webProgramm')
      const webProgTextBlock = document.querySelector(`.skills-webProgramm-textWrapper`)

      const photo = document.querySelector('.skills-photoVideo')
      const photoTextBlock = document.querySelector(`.skills-photoVideo-textWrapper`)
      const photoImg = document.querySelector('.skills-photoVideo-img')

      const audio = document.querySelector('.skills-audio')
      const audioTextBlock = document.querySelector(`.skills-audio-textWrapper`)
      const audioImg = document.querySelector('.skills-audio-img')

      const musicImg = document.querySelector('.skills-music-img')
      const musicTextBlock = document.querySelector(`.skills-music-textWrapper`)

      
      if (e.srcElement.classList[0] === 'skills-webProgramm-img') {
            setTimeout(() => {
                  webProg.classList.add('skills-click')
                  webProgTextBlock.classList.add('text-visible')
                  webProgImg.classList.add('skills-img-click')
                  webProg.classList.remove('small-size')

                  photo.classList.remove('skills-click')
                  photo.classList.remove(`small-size-middle`)
                  photoTextBlock.classList.remove('text-visible')
                  photoImg.classList.remove('skills-img-click')

                  audio.classList.remove('skills-click')
                  audio.classList.remove(`small-size`)
                  audioTextBlock.classList.remove('text-visible')
                  audioImg.classList.remove('skills-img-click')

                  musicImg.classList.remove('skills-img-click')
                  musicTextBlock.classList.remove('text-visible')
            }, 100);
      }

      if (e.srcElement.classList[1] === 'skills-img-click' && webProg.classList.contains(`skills-click`)) {
            setTimeout(() => {
                  webProg.classList.remove('skills-click')
                  webProgTextBlock.classList.remove('text-visible')
                  webProgImg.classList.remove('skills-img-click')
            }, 100);
      }



      if (e.srcElement.classList[0] === 'skills-photoVideo-img') {
            setTimeout(() => {
                  photo.classList.add('skills-click')
                  photoTextBlock.classList.add('text-visible')
                  photoImg.classList.add('skills-img-click')
                  photo.classList.remove('small-size-middle')

                  webProg.classList.add('small-size')
                  webProg.classList.remove('skills-click')
                  webProgTextBlock.classList.remove('text-visible')
                  webProgImg.classList.remove('skills-img-click')

                  audio.classList.remove('skills-click')
                  audio.classList.remove(`small-size`)
                  audioTextBlock.classList.remove('text-visible')
                  audioImg.classList.remove('skills-img-click')

                  musicImg.classList.remove('skills-img-click')
                  musicTextBlock.classList.remove('text-visible')
            }, 100);
      }

      if (e.srcElement.classList[1] === 'skills-img-click' && photo.classList.contains(`skills-click`)) {
            setTimeout(() => {
                  photo.classList.remove('skills-click')
                  photoTextBlock.classList.remove('text-visible')
                  photoImg.classList.remove('skills-img-click')
                  webProg.classList.remove('small-size')
            }, 100);
      }



      if (e.srcElement.classList[0] === 'skills-audio-img') {
            setTimeout(() => {
                  audio.classList.add('skills-click')
                  audioTextBlock.classList.add('text-visible')
                  audio.classList.remove('small-size')
                  audioImg.classList.add('skills-img-click')

                  photo.classList.remove('skills-click')
                  photoTextBlock.classList.remove('text-visible')
                  photo.classList.remove('small-size-middle')
                  photo.classList.add('small-size-middle')
                  photoImg.classList.remove('skills-img-click')

                  musicImg.classList.remove('skills-img-click')
                  musicTextBlock.classList.remove(`text-visible`)

                  webProg.classList.remove('small-size')
                  webProg.classList.remove('skills-click')
                  webProgTextBlock.classList.remove('text-visible')
                  webProgImg.classList.remove('skills-img-click')
            }, 100);
      }

      if (e.srcElement.classList[1] === 'skills-img-click' && audio.classList.contains(`skills-click`)) {
            setTimeout(() => {
                  audio.classList.remove('skills-click')
                  audioTextBlock.classList.remove('text-visible')
                  audioImg.classList.remove('skills-img-click')
                  photo.classList.remove('small-size-middle')
            }, 100);
      }



      if (e.srcElement.classList[0] === 'skills-music-img') {
            setTimeout(() => {
                  musicImg.classList.add('skills-img-click')
                  musicTextBlock.classList.add('text-visible')

                  audio.classList.remove('skills-click')
                  audioTextBlock.classList.remove('text-visible')
                  audio.classList.add('small-size')
                  audioImg.classList.remove('skills-img-click')

                  
                  photo.classList.remove('skills-click')
                  photoTextBlock.classList.remove('text-visible')
                  photo.classList.remove('small-size-middle')
                  photoImg.classList.remove('skills-img-click')

                  webProg.classList.remove('small-size')
                  webProg.classList.remove('skills-click')
                  webProgTextBlock.classList.remove('text-visible')
                  webProgImg.classList.remove('skills-img-click')
            }, 100);
      }

      if (e.srcElement.classList[1] === 'skills-img-click' && musicTextBlock.classList.contains(`text-visible`)) {
            setTimeout(() => {
                  musicImg.classList.remove('skills-img-click')
                  audio.classList.remove('small-size')
                  musicTextBlock.classList.remove('text-visible')
            }, 100);
      }
})