import { cvAside } from "./generateCvAside";
import { cvMain } from "./generateCvMain";

const body = document.querySelector(`body`)

export function generateCvScreen() {
      const cvScreenContainer = document.createElement('div')
      cvScreenContainer.classList.add(`cv-screen-container`)
      body.insertAdjacentElement('beforeend', cvScreenContainer)

      const cvScreenContentWrapper = document.createElement('div')
      cvScreenContentWrapper.classList.add(`cv-content-wrapper`)
      cvScreenContainer.insertAdjacentElement('beforeend', cvScreenContentWrapper)

      //    ASIDE
      const cvScreenAside = document.createElement('div')
      cvScreenAside.classList.add(`aside`)
      cvScreenAside.innerHTML = cvAside
      cvScreenContentWrapper.insertAdjacentElement('beforeend', cvScreenAside)

      //    MAIN
      const cvScreenMain = document.createElement('div')
      cvScreenMain.classList.add(`main`)
      cvScreenMain.innerHTML = cvMain
      cvScreenContentWrapper.insertAdjacentElement('beforeend', cvScreenMain)

      //    FOOTER
      const cvFooter = document.createElement('footer')
      cvFooter.classList.add('cv-footer')
      cvScreenContainer.insertAdjacentElement('beforeend', cvFooter)

      const cvFooterContainer = document.createElement('div')
      cvFooterContainer.classList.add('cv-footer-container')
      cvFooter.insertAdjacentElement('beforeend', cvFooterContainer)

      //    FOOTER NAME
      const cvFooterNamewrapper = document.createElement('div')
      cvFooterNamewrapper.classList.add('cv-footer-container-namewrapper')
      cvFooterContainer.insertAdjacentElement('beforeend', cvFooterNamewrapper)

      const cvFooterNamewrapperText = document.createElement('p')
      cvFooterNamewrapperText.classList.add('cv-footer-container-namewrapper-text')
      cvFooterNamewrapperText.textContent = `Pavel Popov`
      cvFooterNamewrapper.insertAdjacentElement('beforeend', cvFooterNamewrapperText)

      //    FOOTER BACK BUTTON
      const cvFooterBackButtonWrapper = document.createElement('div')
      cvFooterBackButtonWrapper.classList.add('cv-footer-container-backbuttonwrapper')
      cvFooterContainer.insertAdjacentElement('beforeend', cvFooterBackButtonWrapper)

      const cvFooterBackButton = document.createElement('div')
      cvFooterBackButton.classList.add('cv-footer-container-backbuttonwrapper-button')
      cvFooterBackButtonWrapper.insertAdjacentElement('beforeend', cvFooterBackButton)

      const cvFooterBackButtonText = document.createElement('p')
      cvFooterBackButtonText.classList.add('cv-footer-container-backbuttonwrapper-button-text')
      cvFooterBackButtonText.textContent = 'BACK'
      cvFooterBackButton.insertAdjacentElement('beforeend', cvFooterBackButtonText)

      //    FOOTER DOWNLOAD BUTTON
      const cvFooterDawnloadButtonWrapper = document.createElement('div')
      cvFooterDawnloadButtonWrapper.classList.add('cv-footer-container-dawnloadbuttonwrapper')
      cvFooterContainer.insertAdjacentElement('beforeend', cvFooterDawnloadButtonWrapper)

      const cvFooterDawnloadButtonWrapperButton = document.createElement('a')
      cvFooterDawnloadButtonWrapperButton.classList.add('cv-footer-container-dawnloadbuttonwrapper-button')
      cvFooterDawnloadButtonWrapperButton.href = 'assets/img/cv/Portfolio.pdf'
      cvFooterDawnloadButtonWrapperButton.download = 'Popov_Pavel_CV_Junior_FE_Dev'
      cvFooterDawnloadButtonWrapperButton.target="_blank"
      cvFooterDawnloadButtonWrapper.insertAdjacentElement('beforeend', cvFooterDawnloadButtonWrapperButton)

      const cvFooterDawnloadButtonWrapperButtonImg = document.createElement('img')
      cvFooterDawnloadButtonWrapperButtonImg.classList.add('cv-footer-container-dawnloadbuttonwrapper-button-img')
      cvFooterDawnloadButtonWrapperButtonImg.src = `assets/img/cv/download-icon.png`
      cvFooterDawnloadButtonWrapperButton.insertAdjacentElement('beforeend', cvFooterDawnloadButtonWrapperButtonImg)

      const cvFooterDawnloadButtonWrapperButtonText = document.createElement('p')
      cvFooterDawnloadButtonWrapperButtonText.classList.add('cv-footer-container-dawnloadbuttonwrapper-button-text')
      cvFooterDawnloadButtonWrapperButtonText.textContent = `DOWNLOAD CV`
      cvFooterDawnloadButtonWrapperButton.insertAdjacentElement('beforeend', cvFooterDawnloadButtonWrapperButtonText)



      setTimeout(() => {
            cvScreenContainer.classList.add('show-cv-screen')
      }, 50);
}