

document.body.addEventListener('click', (e)=> {
      if (e.srcElement.classList[0] === 'moreinfo-screen-footer-button') {
            document.querySelector('.greeting-screen-container').scrollIntoView({
                  behavior: 'smooth',
                  block: 'start'
            })
            setTimeout(() => {
                  document.querySelector(`.moreinfo-screen-container`).remove()
            }, 700);
      }
})

