const body = document.querySelector(`body`)
const title = [`Calculator`, `Singolo`, `Drum Kit`, `Flex Panel Gallery`, `Custom Video Player`, `Meditation App`, `CSS Variables and JS`, `Whack-A-Mole`, `Raindrops`, `Fancy-Weather`]
const text = [
      `1`,
      `2`,
      `3`,
      `4`,
      `5`,
      `6`,
      `7`,
      `8`,
      `9`,
      `0`
]
const demoUrl = [
      `https://inkvitop.github.io/Calculator/`,
      `https://inkvitop.github.io/MySingolo/`,
      `https://inkvitop.github.io/Drum-kit/`,
      `https://inkvitop.github.io/Flex-panels/`,
      `https://inkvitop.github.io/Custom-Video-Player/`,
      `https://inkvitop.github.io/Meditation-App/`,
      `https://inkvitop.github.io/CSS-Variables/`,
      `https://inkvitop.github.io/Whack-a-mole/`,
      `https://inkvitop.github.io/Raindrops/`,
      `https://inkvitop.github.io/Fancy-weather/`
]
const githubUrl = [
      `https://github.com/inkvitop/RS-projects/tree/Calculator`,
      `https://github.com/inkvitop/RS-projects/tree/Singolo`,
      `https://github.com/inkvitop/RS-projects/tree/Drum-kit`,
      `https://github.com/inkvitop/RS-projects/tree/Flex-panels`,
      `https://github.com/inkvitop/RS-projects/tree/Custom-video-player`,
      `https://github.com/inkvitop/RS-projects/tree/MeditationApp`,
      `https://github.com/inkvitop/RS-projects/tree/Woah!-CSS-Variables!`,
      `https://github.com/inkvitop/RS-projects/tree/Whack-a-mole`,
      `https://github.com/inkvitop/RS-projects/tree/Raindrops`,
      `https://github.com/inkvitop/RS-projects/tree/Fancy-weather`
]


export function generateProjectsScreen() {
      const projectsScreenContainer = document.createElement(`div`)
      projectsScreenContainer.classList.add(`projects-screen-container`)
      projectsScreenContainer.classList.add(`show-projects-screen`)
      body.insertAdjacentElement('beforeend', projectsScreenContainer)

      for (let i = 0; i < 10; i++) {
            //    pROJECTS
            //    pROJECT CONTAINER
            const projectContainer = document.createElement(`div`)
            projectContainer.classList.add(`project-container`)
            projectsScreenContainer.insertAdjacentElement('beforeend', projectContainer)

            //    pROJECT IMG
            const projectContainerImg = document.createElement(`div`)
            projectContainerImg.classList.add(`project-container-img`)
            projectContainerImg.classList.add(`background-${i}`)
            projectContainer.insertAdjacentElement('beforeend', projectContainerImg)

            //    pROJECT TEXT
            const projectContainerWrapper = document.createElement(`div`)
            projectContainerWrapper.classList.add(`project-container-wrapper`)
            projectContainer.insertAdjacentElement('beforeend', projectContainerWrapper)

            //    pROJECT TITLE
            const projectContainerWrapperTitle = document.createElement(`h3`)
            projectContainerWrapperTitle.classList.add(`project-container-wrapper-title`)
            projectContainerWrapperTitle.textContent = title[i]
            projectContainerWrapper.insertAdjacentElement('beforeend', projectContainerWrapperTitle) 

            //    pROJECT TEXT
            const projectContainerWrapperText = document.createElement(`p`)
            projectContainerWrapperText.classList.add(`project-container-wrapper-text`)
            projectContainerWrapperText.textContent = text[i]
            projectContainerWrapper.insertAdjacentElement('beforeend', projectContainerWrapperText) 

            //    pROJECT BUTTONS
            const projectContainerWrapperButtons = document.createElement(`div`)
            projectContainerWrapperButtons.classList.add(`project-container-wrapper-buttons`)
            projectContainerWrapper.insertAdjacentElement('beforeend', projectContainerWrapperButtons) 

            //    pROJECT BUTTON FORM DEMO


            //    pROJECT BUTTON DEMO
            const projectContainerWrapperButtonsDemo = document.createElement(`a`)
            projectContainerWrapperButtonsDemo.classList.add(`project-container-wrapper-buttons-demo`)
            projectContainerWrapperButtonsDemo.textContent = `DEMO`
            projectContainerWrapperButtonsDemo.setAttribute(`href`, `${demoUrl[i]}`)
            projectContainerWrapperButtonsDemo.setAttribute(`target`, `_blank`)
            projectContainerWrapperButtons.insertAdjacentElement('beforeend', projectContainerWrapperButtonsDemo) 


            //    pROJECT BUTTON FORM GITHUB
            const projectContainerWrapperButtonsGithub = document.createElement(`a`)
            projectContainerWrapperButtonsGithub.classList.add(`project-container-wrapper-buttons-git`)
            projectContainerWrapperButtonsGithub.textContent = `GITHUB`
            projectContainerWrapperButtonsGithub.setAttribute(`href`, `${githubUrl[i]}`)
            projectContainerWrapperButtonsGithub.setAttribute(`target`, `_blank`)
            projectContainerWrapperButtons.insertAdjacentElement('beforeend', projectContainerWrapperButtonsGithub) 
      }

      //    FOOTER
      const projectsScreenFooter = document.createElement(`footer`)
      projectsScreenFooter.classList.add(`projects-screen-container-footer`)
      projectsScreenContainer.insertAdjacentElement('beforeend', projectsScreenFooter)

      const projectsScreenFooterContainer = document.createElement('div')
      projectsScreenFooterContainer.classList.add('projects-screen-container-footer-container')
      projectsScreenFooter.insertAdjacentElement('beforeend', projectsScreenFooterContainer)

      //    FOOTER NAME
      const projectsScreenFooterNamewrapper = document.createElement('div')
      projectsScreenFooterNamewrapper.classList.add('projects-screen-container-footer-container-namewrapper')
      projectsScreenFooterContainer.insertAdjacentElement('beforeend', projectsScreenFooterNamewrapper)

      const projectsScreenFooterNamewrapperText = document.createElement('p')
      projectsScreenFooterNamewrapperText.classList.add('projects-screen-container-footer-container-namewrapper-text')
      projectsScreenFooterNamewrapperText.textContent = `Pavel Popov`
      projectsScreenFooterNamewrapper.insertAdjacentElement('beforeend', projectsScreenFooterNamewrapperText)

      //    FOOTER BACK BUTTON
      const projectsScreenFooterBackButtonWrapper = document.createElement('div')
      projectsScreenFooterBackButtonWrapper.classList.add('projects-screen-container-footer-container-backbuttonwrapper')
      projectsScreenFooterContainer.insertAdjacentElement('beforeend', projectsScreenFooterBackButtonWrapper)

      const projectsScreenFooterBackButton = document.createElement('div')
      projectsScreenFooterBackButton.classList.add('projects-screen-container-footer-container-backbuttonwrapper-button')
      projectsScreenFooterBackButtonWrapper.insertAdjacentElement('beforeend', projectsScreenFooterBackButton)

      const projectsScreenFooterBackButtonText = document.createElement('p')
      projectsScreenFooterBackButtonText.classList.add('projects-screen-container-footer-container-backbuttonwrapper-button-text')
      projectsScreenFooterBackButtonText.textContent = 'BACK'
      projectsScreenFooterBackButton.insertAdjacentElement('beforeend', projectsScreenFooterBackButtonText)
}