import anime from 'animejs/lib/anime.es.js';
import { generateMoreInfoSkills } from "./skills/moreInfoSkills";
import { generateContactBlock } from "./contact/moreInfoContact";

const body = document.querySelector(`body`)


export function generateMoreinfoScreen() {
      const moreinfoScreenContainer = document.createElement(`div`)
      moreinfoScreenContainer.classList.add('moreinfo-screen-container')
      body.insertAdjacentElement('beforeend', moreinfoScreenContainer)

      //    HEADER
      const moreinfoScreenHeader = document.createElement('header')
      moreinfoScreenHeader.classList.add('moreinfo-screen-header')
      moreinfoScreenContainer.insertAdjacentElement('beforeend', moreinfoScreenHeader)

      //    HEADER NAME 
      const moreinfoScreenHeaderNamewrapper = document.createElement('div')
      moreinfoScreenHeaderNamewrapper.classList.add('moreinfo-screen-header-namewrapper')
      moreinfoScreenHeader.insertAdjacentElement('beforeend', moreinfoScreenHeaderNamewrapper)

      const moreinfoScreenHeaderNamewrapperText = document.createElement('p')
      moreinfoScreenHeaderNamewrapperText.classList.add('moreinfo-screen-header-namewrapper-text')
      moreinfoScreenHeaderNamewrapperText.textContent = `Pavel Popov`
      moreinfoScreenHeaderNamewrapper.insertAdjacentElement('beforeend', moreinfoScreenHeaderNamewrapperText)

      //    HEADER MENU
      const moreinfoScreenHeaderMenuwrapper = document.createElement('div')
      moreinfoScreenHeaderMenuwrapper.classList.add('moreinfo-screen-header-menuwrapper')
      moreinfoScreenHeader.insertAdjacentElement('beforeend', moreinfoScreenHeaderMenuwrapper)

      //    HEADER MENU SECTIONS
      const moreinfoScreenHeaderwrapperHome = document.createElement('div')
      moreinfoScreenHeaderwrapperHome.classList.add('moreinfo-screen-header-menuwrapper-block')
      moreinfoScreenHeaderwrapperHome.textContent = "HOME"
      moreinfoScreenHeaderMenuwrapper.insertAdjacentElement('beforeend', moreinfoScreenHeaderwrapperHome)


      const moreinfoScreenHeaderwrapperSkills = document.createElement('div')
      moreinfoScreenHeaderwrapperSkills.classList.add('moreinfo-screen-header-menuwrapper-block')
      moreinfoScreenHeaderwrapperSkills.textContent = "SKILLS"
      moreinfoScreenHeaderMenuwrapper.insertAdjacentElement('beforeend', moreinfoScreenHeaderwrapperSkills)


      const moreinfoScreenHeaderwrapperContact = document.createElement('div')
      moreinfoScreenHeaderwrapperContact.classList.add('moreinfo-screen-header-menuwrapper-block')
      moreinfoScreenHeaderwrapperContact.textContent = "CONTACT"
      moreinfoScreenHeaderMenuwrapper.insertAdjacentElement('beforeend', moreinfoScreenHeaderwrapperContact)


      //    SKILLS
      const moreinfoScreenSkills = document.createElement('div')
      moreinfoScreenSkills.classList.add('moreinfo-screen-skills')
      moreinfoScreenContainer.insertAdjacentElement('beforeend', moreinfoScreenSkills)
      
      generateMoreInfoSkills()


      //    CONTACT
      const moreinfoScreenContact = document.createElement('div')
      moreinfoScreenContact.classList.add('moreinfo-screen-contact')
      moreinfoScreenContainer.insertAdjacentElement('beforeend', moreinfoScreenContact)

      generateContactBlock()


      //    FOOTER
      const moreinfoScreenFooter = document.createElement('footer')
      moreinfoScreenFooter.classList.add('moreinfo-screen-footer')
      moreinfoScreenContainer.insertAdjacentElement('beforeend', moreinfoScreenFooter)

      const moreinfoScreenFooterBackButton = document.createElement('div')
      moreinfoScreenFooterBackButton.classList.add('moreinfo-screen-footer-button')
      moreinfoScreenFooter.insertAdjacentElement('beforeend', moreinfoScreenFooterBackButton)

      const moreinfoScreenFooterBackButtonArrow = document.createElement('img')
      moreinfoScreenFooterBackButtonArrow.classList.add('moreinfo-screen-footer-arrow')
      moreinfoScreenFooterBackButtonArrow.src = `assets/img/moreInfo/arrow.png`
      moreinfoScreenFooterBackButton.insertAdjacentElement('beforeend', moreinfoScreenFooterBackButtonArrow)

      const moreinfoScreenFooterText = document.createElement('p')
      moreinfoScreenFooterText.classList.add('moreinfo-screen-footer-text')
      moreinfoScreenFooterText.innerHTML = `&#169; Pavel Popov Minsk 2021`
      moreinfoScreenFooter.insertAdjacentElement('beforeend', moreinfoScreenFooterText)


      anime({
            targets: '.moreinfo-screen-footer-arrow',
            keyframes: [
                  {translateY: 0},
                  {translateY: 5},
                  {translateY: -5},
                  {translateY: 0},
                  ],
            duration: 1300,
            easing: 'spring(1, 80, 10, 0)',
            loop: true,
            autoplay: true,
      });
}